@import '../pages/styles.scss';

.signup{
    form{
        font-size: large;
        margin: auto;
        width: 100%;
        margin-bottom: 2rem;
      fieldset{
          padding-top: 40px;
          border: medium solid $primary-color;
          border-radius: 15px;
    
    legend{
            border-radius: 5px;
            width: fit-content;
        }
        input{
            height: 40px;
            width: 90%;
            margin-bottom: 20px;
            margin-left: 30px;
            border: 2px solid $layout-color;
            border-radius: 7px;
        }
        label{
            margin-left: 30px;
        }
    
        button{
            height: 40px;
            margin-bottom: 20px;
            margin-left: 30px;
            color: white;
            background-color: $primary-color;
            border: 2px solid $layout-color;
            border-radius: 7px;
        }
        }
    
        
    
    }
}


$breakpoint-tablet: 860px;
@media only screen and (max-width: $breakpoint-tablet) {


    .container{
        width: 100%;
     .heading{
         margin: auto;
         text-align: center;
         width: 100%;
         }
     .account{
         min-height: 20em;
         display: flex;
         flex-direction: row;
         margin: 0;

     .myaccount{
       
     form{
         font-size: large;
         margin: 0;
         margin-bottom: 0.7em;
         width: 100%;

       fieldset{
           padding-top: 40px;
           border: medium solid $primary-color;
           border-radius: 15px;
           width: 100%;

     legend{
             border-radius: 0.2em;
             width: fit-content;
 
         }
         input{
             height: 40px;
             width: 90%;
             margin-bottom: 20px;
             margin-left: 0.2em;
             border: 2px solid $layout-color;
             border-radius: 7px;
         }
         label{
            margin-left: 0.2em;
        }
     
         button{
             height: 40px;
             margin-bottom: 0.6em;
             margin-left: 30px;
             color: white;
             background-color: $primary-color;
             border: 2px solid $layout-color;
             border-radius: 7px;
         }
         }
     
         
     
     }
     
     }
     }
    }
     
}
